@media only screen and (min-width: 1000px) {
  body {
    background-color: hsl(0, 100%, 74%);
  }

  .bg{
    width: 100%;
    height: 100%;
  }

  .h-txt{
    color: ivory;
    position: relative;
    top: -550px;
    left: 8%;
    width: 350px;
    font-size: 35px;
  }

  .s-txt {
    color: ivory;
    position: relative;
    top: -550px;
    left: 8%;
    width: 450px;
    font-size: 15px;
  }

  .form {
    background-color: ivory;
    position: relative;
    top: -825px;
    left: 43%;
    width: 410px;
    height: 350px;
    padding-left: 26px;
    padding-top: 26px;
    border-radius: 10px;
  }

  .input {
    border: 1px solid gray;
    padding: 13px;
    padding-left: 22px;
    padding-right: 185px;
    border-radius: 3px;
    font-weight: 700;
  }

  .input:focus{
    border-color: purple;
    outline: none;
  }

  .container {
    color: ivory;
    background-color: hsl(248, 32%, 49%);
    padding: 15px;
    text-align: center;
    width: 409px;
    position: relative;
    top: -1280px;
    left: 43%;
    border-radius: 10px;
    font-size: 12px;
  }

  .bold {
    font-weight: 700;
    color: ivory;
  }

  .f-button {
    padding: 13px;
    padding-right: 105px;
    padding-left: 105px;
    text-align: center;
    border-radius: 5px;
    background-color: hsl(154, 59%, 51%);
    color: ivory;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    border: none;
    position: absolute;
    left: 25px;
    top: 75%;
  }

  .f-button:hover {
    cursor: pointer;
  }

  .fp {
    font-size: 11px;
    text-align: center;
    color: rgb(170, 170, 170);
    position: relative;
    left: -15px;
    top: 45px;
  }

  .ts {
    font-weight: 600;
    color: hsl(0, 100%, 74%);
  }


}

